import React from 'react';
import './Toggler.scss';

export default class Toggler extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      istoggle: this.props.value,
    };
  }

  togglerchange = () => {
    this.setState(
      prevState => {
        return { istoggle: !prevState.istoggle };
      },
      () => {
        if (
          this.props.onoffstatus &&
          typeof this.props.onoffstatus == 'function'
        ) {
          this.props.onoffstatus(this.state);
        }
      }
    );
  };

  render() {
    return (
      <div className={'Toggle ' + this.props.DataToggle}>
        <div
          className={this.state.istoggle ? 'Toggler togglerchange' : 'Toggler'}
          onClick={this.togglerchange}
        >
          ON
        </div>
        <div
          className={this.state.istoggle ? 'Toggler' : 'Toggler togglerchange'}
          onClick={this.togglerchange}
        >
          OFF
        </div>
      </div>
    );
  }
}
