import React from 'react';
import moment from 'moment';
import './TurnTemplates.scss';

class TurnTemplates extends React.Component {
  state = {};
  date = value => {
    let val = value !== undefined ? value : '';
    var now = new Date(val);
    console.log(now, 'date');
    var dateString = moment(now).format('DD MMM YY ');
    return dateString.toUpperCase();
  };

  time = value => {
    let val = value !== undefined ? value : '';
    var str = val.slice(0, -1);
    var now = new Date(str);
    var timestring = moment(now).format('HH:mm');
    return timestring;
  };

  clicked = val => {
    if (this.props.templateclick) {
      console.log('from turn templates', val);
      this.props.templateclick(val);
    }
  };

  render() {
    return (
      <div className="Flights">
        {this.props.sort.map((value, index) => (
          <div
            className="Flight_Box"
            key={index}
            onClick={() => {
              this.clicked(value);
            }}
          >
            <div className="Flights_Details">
              <div className="Flight_Number">
                {value.turntemplateid}
                <div className="BaseBody">
                  {value.airlineaircrafttype} - {value.fleettype}{' '}
                </div>
                {/* <div className="BaseBody">{value.turntemplatename} | {value.versionno}</div>  */}
              </div>
              <div className="Base_Details">{value.turntemplatename}</div>
            </div>
            <div className="_Date_Time">
              <div className="_Date1">
                <div className="header margin-left">Created On</div>
                <div className="displayflex">
                  <div>{this.date(value.createddate)}</div>
                  <div className="_Time1">{this.time(value.createddate)}</div>
                </div>
              </div>

              <div className="_Date1">
                <div className="header">Last Modified On </div>
                <div className="displayflex">
                  <div>{this.date(value.lastmodifieddate)} </div>
                  <div className="_Time1">
                    {this.time(value.lastmodifieddate)}
                  </div>
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>
    );
  }
}

export default TurnTemplates;
