import React from 'react';
import InputElement from '../InputElement';
import './search-box.scss';
import { MdSearch } from 'react-icons/md';
const SearchBox = ({ className, onChange, list, filterBy, ...props }) => {
  const updateContent = event => {
    // getting searchbox value
    const searchValue = event.target.value.toString().toLowerCase();

    // using filter function to get updated list
    const newList =
      Array.isArray(list) && filterBy.length
        ? list.filter(list_item => {
            //check whether searchbox values are available in list
            const isTrue = filterBy.findIndex(filterContent => {
              return list_item[filterContent]
                .toLowerCase()
                .includes(searchValue);
            });
            return isTrue === -1 ? false : true;
          })
        : list;
    console.log(newList.length, list.length);
    // pass onchange handler with event and updated list
    onChange(event, newList);
  };
  return (
    <div className="search-box">
      <MdSearch className={'search-icon search-' + className} />
      <InputElement
        className={' search-bar ' + className}
        onChange={updateContent}
        {...props}
      />
    </div>
  );
};

SearchBox.defaultProps = {
  /* classname for searchbox */
  className: '',
  /* onchange handler to pass the data of EVENT and updated LIST */
  onChange: () => {},
  /* list of object should be given for filter */
  list: [],
  /* filter using FILTERBY content, 
    filterby content should be array
  */
  filterBy: [],
};

export default SearchBox;
