import React, { Component } from 'react';
import './Dependencytask.scss';
import InputElement from '../../../../Atoms/InputElement';
import Toggler, { TogglerUsingParent } from '../../../../Atoms/Toggler';
import SearchBox from '../../../../Atoms/SearchBox';
import { Taskdetails } from '../SelectTasks';

class Dependencytask extends Component {
  state = {
    searchdependencytask: '',
    list: [],
    selectedOrder: [],
    // tick: false,
  };
  searchhandler = (e, list) => {
    this.setState({ searchdependencytask: e.target.value, list });
  };
  orderDetails = data => {
    let { selectedOrder } = this.state;
    let newData = [];
    if (selectedOrder.length === 0) {
      newData.push(data);
    } else {
      let count = 0;
      selectedOrder.map(order => {
        if (!(order.taskid === data.taskid)) {
          newData.push(order);
        } else {
          count = 1;
        }
      });
      if (count === 0) {
        newData.push(data);
      }
    }
    selectedOrder = [...selectedOrder, data];
    if (
      this.props.selectedDependencytask &&
      typeof this.props.selectedDependencytask == 'function'
    ) {
      this.props.selectedDependencytask([...newData]);
    }
    this.setState({
      selectedOrder: newData,
    });
  };

  showOrders = () => {
    console.log(this.props.mainTaskCurrent, 'ordertask');
    return (
      this.props.mainTaskCurrent.dependencytaskid !== undefined &&
      this.props.mainTaskCurrent.dependencytaskid.map(val => {
        return <div>{val}</div>;
      })
    );
  };

  render() {
    //let tofilter = this.props.dependencytask.data;

    return (
      <>
        <div className="dependencytask">
          {this.props.mainTask.map(val => {
            if (val.taskid !== this.props.mainTaskCurrent.taskid) {
              return null;
            }
            return (
              <>
                <div style={{ display: 'flex ' }}>
                  <div className="taskdurationtext">Task Duration Time</div>
                  {/* <div className="mandatory-star">&nbsp;*</div> */}
                </div>
                <div className="startendtime">
                  <div className="starttime">
                    {' '}
                    <InputElement
                      placeholder="Start"
                      value={val.taskstart}
                      onChange={e => {
                        // this.timehandler(e.target.value);
                        this.props.durationHandler(
                          this.props.mainTaskCurrent,
                          e.target.value,
                          'start'
                        );
                      }}
                    />
                  </div>
                  <div className="endtime">
                    <InputElement
                      value={val.taskend}
                      placeholder="End"
                      onChange={e => {
                        this.props.durationHandler(
                          this.props.mainTaskCurrent,
                          e.target.value,
                          'end'
                        );
                      }}
                    />
                  </div>
                </div>
                <div className="dcptext">DCP Include</div>
                <Toggler
                  value={val.isDCPinclude}
                  onoffstatus={() => {
                    this.props.DCPHandler(this.props.mainTaskCurrent);
                  }}
                />

                <div className="show-order">{this.showOrders()}</div>
                <div className="selectdeptasktext">Select Dependency task</div>
                <SearchBox
                  placeholder="Search By Task Name"
                  className="dependencysearchbar"
                  //list = {tofilter}
                  onChange={this.searchhandler}
                  filterBy={['tasknm']}
                />
              </>
            );
          })}
          <div style={{ marginBottom: '50px' }}>
            <Taskdetails
              task={'Subtask'}
              // dependencytask={this.dependencytask}
              callback={this.props.callback}
              selectedtask={this.props.selectedtask}
              searchselectedtask={this.state.searchdependencytask}
            />
          </div>
        </div>
      </>
    );
  }
}

export default Dependencytask;
