import React, { useState } from 'react';
import { SlideDown } from 'react-slidedown';
import 'react-slidedown/lib/slidedown.css';

const Slide = ({
  open = false,
  content = null,
  header = null,
  sliderStatus,
}) => {
  const [isOpen, setOpen] = useState(open);
  console.log('hi from slide');
  const handleToggler = () => {
    setOpen(!isOpen);
    sliderStatus(isOpen);
  };
  return (
    <div
      className={`slider ${isOpen ? 'active-slide' : ''}`}
      style={{ cursor: 'pointer' }}
    >
      <div className="slider-header" onClick={handleToggler}>
        {header}
      </div>
      <SlideDown className={'my-dropdown-slidedown'}>
        {isOpen ? content : null}
      </SlideDown>
    </div>
  );
};

Slide.defaultProps = {
  sliderStatus: () => {},
};

export default Slide;
