import React, { Component } from 'react';
import PropTypes from 'prop-types';
import './tab.scss';

import Tab from './tab';

class Tabs extends Component {
  static propTypes = {
    children: PropTypes.instanceOf(Array).isRequired,
  };

  constructor(props) {
    super(props);

    this.state = {
      activeTab: this.props.children[0] && this.props.children[0].props.label,
    };
  }

  onClickTabItem = tab => {
    this.setState({ activeTab: tab });
  };

  render() {
    const {
      onClickTabItem,
      props: { children },
      state: { activeTab },
    } = this;
    console.log(this.props.children, 'children');
    return (
      <div className="tabs">
        <ul className="tab-list">
          {children.map(child => {
            const { label, className } = child.props;

            return (
              <Tab
                activeTab={activeTab}
                key={label}
                label={label}
                onClick={onClickTabItem}
                className={className}
              />
            );
          })}
        </ul>
        <div>
          {children.map(child => {
            if (child.props.label !== activeTab) return undefined;
            return child;
          })}
        </div>
      </div>
    );
  }
}

export default Tabs;
