import React, { Component } from 'react';
import './CreateTurnType.scss';
import InputElement from '../../../../Atoms/InputElement';
import Button from '../../../../Atoms/Button';
import { commonAction } from '../../../../../Store/commonActivity/action';

class CreateTurnType extends Component {
  obj = {
    turntype: '',
    turndescription: '',
  };
  checkallfields = () => {
    if (this.obj.turntype === '') {
      commonAction.openAlert({
        title: 'TurnType',
        content: 'Turn Name Must be Filled',
      });
      return false;
    } else if (this.obj.turndescription === '') {
      commonAction.openAlert({
        title: 'TurnType',
        content: 'Turn Description  Must be Filled',
      });
      return false;
    }
    this.props.CreateTurnType(false);
    this.props.TrunTypeSubmit(this.obj);
  };
  render() {
    return (
      <>
        <div>
          <div className="CreateTurnNameText">Create TurnType</div>
          <div className="TurnDetails">
            <div style={{ display: 'flex ' }}>
              <div className="TurnName">Turn Name</div>
              <div className="mandatory-star">&nbsp;*</div>
            </div>
            <InputElement
              className="TurnTypeInputelements"
              placeholder="Enter"
              onChange={e => {
                this.obj.turntype = e.target.value;
              }}
            />
            <div style={{ display: 'flex ' }}>
              <div className="TurnName">Description</div>
              <div className="mandatory-star">&nbsp;*</div>
            </div>
            <textarea
              type="text"
              placeholder="Enter"
              className="TurnTypeInputelements TurnTypeDescription"
              onChange={e => {
                this.obj.turndescription = e.target.value;
              }}
            ></textarea>
            {/* <div className='TurnTypeInputelements TurnTypeDescription' onClick={this.adddescription}></div> */}
            <div className="buttondesign">
              <Button
                className="save-btn"
                ButtonHandler={() => {
                  this.checkallfields();
                }}
              >
                Save
              </Button>
              <Button ButtonHandler={() => this.props.CreateTurnType(false)}>
                Cancel
              </Button>
            </div>
          </div>
        </div>
      </>
    );
  }
}
export default CreateTurnType;
