import React, { Component } from 'react';
import SelectTasks from '../SelectTasks';
import Dependencytask from '../Dependencytask';
import './ViewTemplate_AddTask.scss';
import moment from 'moment';
import { connect } from 'react-redux';
import { ViewTemplate } from '../../../../../Store/TurnManagement/Tep_View_Del_Task/action';
import alert from '../../../../../Utils/commonActivity/alert/alert';
import { commonAction } from '../../../../../Store/commonActivity/action';

class ViewTemplate_AddTask extends Component {
  state = {
    TemplateDetails: '',
    subtaskresponse: false,
    Maintask: [],
    selectedOrderForMainTask: [],
    selectedDependency: [],
  };
  constructor(props) {
    super(props);
  }
  componentDidMount() {
    this.generateTurn(this.props.dependency.data);
    //console.log(this.props.dependency.data, 'dataaaaaaa');
  }
  generateTurn = Maintask => {
    let SubTask = [...this.props.dependency.data]; //41

    let filteredMaintask = [];
    let taskId = [];
    this.props.Existingtask &&
      this.props.Existingtask.map(existing => {
        taskId.push(existing.taskid);
      });
    Maintask.map(task => {
      if (!taskId.includes(task.taskid)) {
        filteredMaintask.push({ ...task });
      }
    });

    let task = filteredMaintask.map((maintask, index) => {
      const temp = { ...maintask };
      console.log(temp, 'crash temp');
      temp.tick = false;
      temp.isActive = false;
      temp.dependencytaskid = [];
      temp.cpminclind = false;
      temp.taskstart = '';
      temp.taskend = '';
      let SubTasktemp = [];
      SubTask.forEach((item, i) => {
        const newItem = { ...item };
        newItem.parent = temp.taskid;
        newItem.tick = false;
        if (newItem.taskid != temp.taskid) {
          SubTasktemp.push(newItem);
        }
      });
      temp.tasks = [...SubTasktemp];
      return temp;
    });
    if (task.length >= 1) {
      console.log(task, 'crash');
      this.setState({ Maintask: task });
    }
  };

  callback = (item, taskname, index) => {
    console.log(item, taskname, index);
    let temp = [...this.state.Maintask];
    let { selectedOrderForMainTask = [], selectedDependency } = this.state;
    if (taskname === 'Maintask') {
      temp = temp.map(val => {
        val.isActive = false;
        if (val.taskid === item.taskid) {
          item.tick = !item.tick;

          if (item.tick) {
            item.isActive = true;
            selectedDependency = item.tasks;
            let isAlreadySelected = false;
            selectedOrderForMainTask.forEach(selected => {
              if (selected.taskid == item.taskid) {
                isAlreadySelected = true;
              }
            });
            if (!isAlreadySelected) {
              selectedOrderForMainTask.push(item);
            }
          } else {
            selectedOrderForMainTask = selectedOrderForMainTask.filter(
              selected => selected.taskid != item.taskid
            );
          }
          return item;
        }

        return val;
      });
      this.setState({
        Maintask: temp,
        selectedDependency,
        selectedOrderForMainTask,
      });
    } else {
      console.log(temp, item, 'tempitem');
      item.tick = !item.tick;
      temp.forEach(main => {
        if (main.taskid === item.parent) {
          if (item.tick) {
            main.dependencytaskid.push(item.taskid);
          } else {
            main.dependencytaskid = main.dependencytaskid.filter(
              val => !(val === item.taskid)
            );
          }
        }
      });
    }

    // selectedOrderForMainTask.dependencytaskid;

    this.setState({});

    // this.setState({ Maintask: task });
  };

  DCPHandler = currentparent => {
    //   alert();
    console.log(currentparent);
    currentparent.cpminclind = !currentparent.cpminclind;
    this.setState({});
  };

  durationHandler = (currentparent, value, condition) => {
    //   alert();

    if (condition === 'start') {
      currentparent.taskstart = value;
    } else {
      currentparent.taskend = value;
    }
    this.setState({});
  };

  savetask = () => {
    let currentdate =
      moment(new Date())
        .toISOString()
        .split('.')[0] + 'Z';

    let task = this.state.Maintask.filter(val => val.tick === true);

    task = task.map((item, i) => {
      let copyobject = { ...item };
      if (copyobject.tick === true) {
        if (copyobject.cpminclind === true) {
          copyobject.cpminclind = 'Y';
        } else {
          copyobject.cpminclind = 'N';
        }
        // let taskorder = this.props.templateresponse[0].tasks.length;
        copyobject.dependencytaskid = copyobject.dependencytaskid.map(
          (item, index) => {
            return { taskid: item, taskorder: index + 1 };
          }
        );
        copyobject.lastmodifieddate = currentdate;

        delete copyobject['tasks'];
        delete copyobject['tick'];
        delete copyobject['isActive'];
        delete copyobject['id'];
      }
      return copyobject;
    });
    let previoustask =
      this.props.templateresponse[0].tasks &&
      this.props.templateresponse[0].tasks.length;
    let copytemplate = {
      ...(this.props.templateresponse[0] && this.props.templateresponse[0]),
    };
    task.map(
      (item, i) =>
        (item.taskorder =
          copytemplate.tasks && copytemplate.tasks.length + i + 1)
    );

    task.map(item => {
      if (item.dependencytaskid.length >= 1) {
        let striged = item.dependencytaskid.map(string => {
          return JSON.stringify(string).replace(/"/g, "'");
        });
        item.dependencytaskid = striged;
        return item;
      }
      return item;
    });
    console.log(copytemplate, 'inside if ');
    let finalobj;
    // if (copytemplate.tasks !== null) {
    finalobj = {
      ...copytemplate,
      tasks: [...[...copytemplate.tasks], ...[...task]],
    };
    // }
    delete finalobj['turntemplateid'];
    delete finalobj['lastmodifieddate'];
    finalobj.lastmodifieddate = currentdate;
    console.log(finalobj, 'atleast');

    // let taskslength = finalobj.tasks.length;

    if (finalobj.tasks.length === previoustask) {
      console.log('inside addtaskif');
      commonAction.openAlert({
        title: 'ALERT',
        content: 'Atleast OneTask Should be Selected',
      });
      this.props.addtaskcall(false);
      return false;
    }
    console.log(task, 'addtask');
    console.log(finalobj, 'taskviewfromfinalobj');
    this.props.AddtasktoExistingTemplate(finalobj);
    this.props.addtaskcall(false);
  };

  render() {
    console.log(this.props.templateresponse, 'templateresponse');
    if (this.props.postcallofaddtask === true) {
      this.savetask();
    }

    let Maintask = this.props.dependency.data || []; //41
    console.log(Maintask, 'maintask');
    let { selectedOrderForMainTask } = this.state;
    let currentparent = [];
    if (selectedOrderForMainTask.length) {
      currentparent =
        selectedOrderForMainTask[selectedOrderForMainTask.length - 1];
      currentparent.isActive = true;
      selectedOrderForMainTask = currentparent.tasks;
    }
    // if (
    //   // this.state.Maintask.length >= 1 &&
    //   this.state.Maintask &&
    //   this.state.Maintask.length !==
    //     Maintask.length - this.props.Existingtask.length
    // ) {
    //   console.log(
    //     this.props.Existingtask.length,
    //     this.state.Maintask.length,
    //     Maintask.length - this.props.Existingtask.length,
    //     'length'
    //   );
    //   this.generateTurn(Maintask);
    // }

    console.log(this.state.Maintask, 'Maintask');
    console.log(this.state.selectedDependency, 'selectedDependency');
    console.log(
      this.state.selectedOrderForMainTask,
      'selectedOrderForMainTask'
    );

    ///FOR SHOWING HOLE TASK COUNT //
    let counter = 0;
    this.state.Maintask.map((item, index) => {
      if (item.tick === true) {
        counter = counter + 1;
      }
    });
    console.log(this.props.dependency, 'Maintaskview');
    return (
      <div>
        <div className="select_Task">Selected Tasks - {counter}</div>
        <div style={{ display: 'flex' }}>
          <SelectTasks
            selectedtask={this.state.Maintask}
            callback={this.callback}
            departmentresponse={this.props.departmentresponse}
          />
          <Dependencytask
            selectedtask={selectedOrderForMainTask}
            mainTask={this.state.Maintask}
            mainTaskCurrent={currentparent}
            callback={this.callback}
            DCPHandler={this.DCPHandler}
            durationHandler={this.durationHandler}
          />
        </div>
      </div>
    );
  }
}

const mapDispatchToProps = {
  AddtasktoExistingTemplate: ViewTemplate.AddTask,
};
const mapStateToProps = state => {
  return {};
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ViewTemplate_AddTask);
