import React, { Component } from 'react';
import './DepartmentList.scss';
import DeleteIcon from '../../../../Img/delete.png';

class DepartmentList extends Component {
  render() {
    const val = this.props.DepartmentResponse;
    return (
      <>
        {val !== 'undefined' ? (
          <div>
            <div className="DepartmentlistText">Department List</div>
            <div className="Departmentlist">
              {val.data.map((value, index) => (
                <div className="Departmentlistwrap" key={index}>
                  <div className="Departmentid">{value.id}</div>
                  <div className="Departmentname">{value.departmentname}</div>
                  <div className="Departmentcode">
                    {value.departmentcode}
                    <div className="delete-icon-div">
                      <img
                        onClick={() =>
                          this.props.handledelete({ id: value.id })
                        }
                        src={DeleteIcon}
                        width="20px"
                        height="20px"
                        alt="delete"
                      />
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        ) : (
          <></>
        )}
      </>
    );
  }
}

export default DepartmentList;
