import React, { Component } from 'react';
import './SaveCancelButton.scss';
import Button from '../Button';

class SaveCancelButton extends Component {
  state = {};
  render() {
    return (
      <>
        <div className="savecancelbutton">
          <div className="wrapbuttons">
            <Button
              ButtonHandler={() => {
                this.props.CancelButtonHandler();
              }}
            >
              Cancel
            </Button>
            <Button
              className="save-btn"
              ButtonHandler={() => {
                this.props.DoneButtonHandler();
              }}
            >
              Done
            </Button>
          </div>
        </div>
      </>
    );
  }
}

export default SaveCancelButton;
