import React, { Component } from 'react';
import CreateTurnType from '../CreateTurnType';
import CreateButton from './../../../../Atoms/CreateButton';
import TurnTypeList from '../TurnTypeList';
import { connect } from 'react-redux';
import { ManageTurnTypeActions } from '../../../../../Store/ManageTurnType/action';

class ManageTurnType extends Component {
  state = {
    isCreateTurnType: false,
  };
  CreateTurnType = isOpen => {
    this.setState(prevState => {
      return { isCreateTurnType: isOpen };
    });
  };
  TrunTypeSubmit = obj => {
    console.log('new', obj);
    this.props.TurnTypesubmit(obj);
  };
  render() {
    return (
      <>
        {this.state.isCreateTurnType ? (
          <CreateTurnType
            CreateTurnType={this.CreateTurnType}
            TrunTypeSubmit={this.TrunTypeSubmit}
          />
        ) : (
          <CreateButton
            text="Create Turn Type"
            CreateButtonHandler={() => {
              this.CreateTurnType(true);
            }}
          />
        )}
        <TurnTypeList
          TurnTypeResponse={this.props.turntypeinitialresponse}
          handledelete={val => this.props.TurnTypeDelete(val)}
        />
      </>
    );
  }
}

const mapDispatchToProps = {
  TurnTypesubmit: ManageTurnTypeActions.postTurnTypeDetails,
  TurnTypeDelete: ManageTurnTypeActions.deleteTurnTypeDetails,
};
const mapStateToProps = state => {
  return {
    turntypeinitialresponse: state.Manageturntypeinitialdatareducer,
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(ManageTurnType);
