import React, { Component } from 'react';
import InputElement from '../../../../Atoms/InputElement';
import Button from '../../../../Atoms/Button';
import { MdSearch } from 'react-icons/md';
import { GoPlus } from 'react-icons/go';
import './Header.scss';

class Header extends Component {
  state = {
    Search:false
  };

  Createturntemplate = () => {
    if (
      this.props.Createturntemplate &&
      typeof this.props.Createturntemplate === 'function'
    ) {
      this.props.Createturntemplate();
    }
  };

  render() {
    return (
      <div className="TurnManagementHeader">
        <div className="TurnManagementwrap">
          <div className="TurnManagementText">Turn Management</div>
          <div className="Turn-header-right-content">
            <div className="turn-search-header">
              <MdSearch className="turn-search-icon" />
              <InputElement
                className={this.state.Search ? "TurnManagementSearchBar searchingtext":"TurnManagementSearchBar"}
                placeholder="Template Name / ID"
                onChange={e =>{ this.props.TemplateSearch(e.target.value);this.setState({Search:true})}}
              />
            </div>
            <div
              className="turn-header-create"
              onClick={this.Createturntemplate}
            >
              <GoPlus className="turn-create-icon" />
              <Button className="CreateTurnTemplate">
                Create Turn Template
              </Button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Header;
