import React, { Component } from 'react';
import './../DepartmentList/DepartmentList.scss';
import './TurnType.scss';
import DeleteIcon from './../../../../Img/delete.png';

class TurnTypeList extends Component {
  state = {};
  render() {
    const val = this.props.TurnTypeResponse.data;
    return (
      <>
        <div className="DepartmentlistText">Turn Type</div>
        <div className="Departmentlist">
          {val.map((value, index) => (
            <div className="Departmentlistwrap" key={index}>
              <div className="Departmentid">{value.id}</div>
              <div className="Departmentname">
                {value.turntype}
                <div className="Departmentdescription">
                  {value.turndescription}
                </div>
              </div>
              <div className="Departmentcode">
                <div className="delete-icon-div">
                  <img
                    onClick={() => this.props.handledelete({ id: value.id })}
                    src={DeleteIcon}
                    width="20px"
                    height="20px"
                    alt="delete"
                  />
                </div>
              </div>
            </div>
          ))}
        </div>
      </>
    );
  }
}

export default TurnTypeList;
