import React from 'react';
import Accordion from './../../../../Atoms/Accordion';
import './FlightTurnManagement.scss';
import Header from './../Header';
import { connect } from 'react-redux';
import { TemplateListActions } from '../../../../../Store/TemplateList/action';
import ManageTask from '../ManageTask';
import ManageDepartment from '../ManageDepartment';
import ManageTurnType from '../ManageTurnType';
import TurnTemplates from './../TurnTemplates';
import { ManageDepartmentActions } from '../../../../../Store/ManageDepartment/action';
import { ManageTurnTypeActions } from '../../../../../Store/ManageTurnType/action';
import { ManageTaskActions } from '../../../../../Store/ManageTask/action';
import TemplateView from '../TemplateView';
import { ViewTemplateHeader } from './../TemplateView';
import CreateTurnTemplate from '../CreateTurnTemplate';
import { CreateTurnTemplateHeader } from '../CreateTemplateTemplateDetails';
import SaveCancelButton from '../../../../Atoms/SaveCancelButton';
import { StationActions } from '../../../../../Store/Station/action';
import { AircraftTypeActions } from '../../../../../Store/AircraftType/action';

class FlightTurnManagement extends React.Component {
  state = {
    TemplateSearch: '',
    currentTemplateID: null,
    pageToNavigate: 'turnTemplate',
    Addtaskfooter: false,
    postcallofaddtask: '',
  };

  childTaskDetails: {
    tasks: [],
  };
  componentDidMount() {
    this.props.templateinitialdata();
    this.props.departmentinitialdata();
    this.props.TurnTypeInitialdata();
    this.props.taskinitialdata();
    this.props.stationinitialdata();
    this.props.initialactypedata();
  }

  TemplateSearch = e => {
    this.setState({
      TemplateSearch: e,
    });
  };
  handleback = () => {
    this.setState({
      pageToNavigate: 'turnTemplate',
      Addtaskfooter: false,
    });
  };
  sort = val => {
    let iteration = val;
    if (this.state.TemplateSearch !== '') {
      iteration = val.filter(item => {
        console.log(item.turntemplatename, 'search crash');
        if (
          (item &&
            item.turntemplateid
              .toLowerCase()
              .match(this.state.TemplateSearch.toLowerCase())) ||
          (item.turntemplatename !== null &&
            item.turntemplatename
              .toLowerCase()
              .match(this.state.TemplateSearch.toLowerCase()))
        ) {
          return item;
        }
      });
    }
    return iteration;
  };
  Equipfleetcatagory = val => {
    let equipfleet = [];
    val.map(item => {
      equipfleet.push({
        equipmenttype: item.internalaircrafttype,
        fleetcategory: item.fleetcategory,
      });
    });
    return equipfleet;
  };
  addtaskcall = val => {
    this.setState({
      postcallofaddtask: val,
    });
    this.setState({
      Addtaskfooter: false,
    });
  };
  pageToNavigate = (templateidname, sort) => {
    console.log(this.childTaskDetails, 'childTaskDetails');
    if (this.state.pageToNavigate === 'turnTemplate') {
      return (
        <div>
          <Header
            TemplateSearch={this.TemplateSearch}
            Createturntemplate={() => {
              this.setState({
                pageToNavigate: 'CreateTurntemplate',
                TemplateSearch: '',
              });
            }}
          />{' '}
          <div className="TurnManagement">
            <div className="Template">
              <TurnTemplates
                sort={sort}
                templateclick={val => {
                  console.log(val, '-->value click');
                  // this.childTaskDetails = val;
                  this.setState({
                    currentTemplateID: val.id,
                    pageToNavigate: 'ViewTemplate',
                  });
                }}
              />{' '}
            </div>{' '}
            <div className="Manage">
              <span key={'manage'}>
                <Accordion menuList={Menu} />{' '}
              </span>{' '}
            </div>{' '}
          </div>{' '}
        </div>
      );
    } else if (this.state.pageToNavigate === 'ViewTemplate') {
      console.log(this.state.Addtaskfooter, 'state');
      return (
        <div>
          <ViewTemplateHeader
            templatedetails={templateidname}
            navigateback={this.handleback}
          />{' '}
          <div className="TurnManagement">
            <TemplateView
              templatedetails={templateidname}
              templateresponse={this.props.templateresponse}
              task={this.childTaskDetails}
              isFooterEnabled={this.state.Addtaskfooter}
              dependency={this.props.taskinitialresponse}
              AddtaskConst={state =>
                this.setState({
                  Addtaskfooter: true,
                })
              }
              // departmentinitialresponse={this.props.departmentinitialresponse}
              departmentresponse={this.props.departmentinitialresponse}
              postcallofaddtask={this.state.postcallofaddtask}
              addtaskcall={this.addtaskcall}
            />{' '}
            <div className="Manage">
              <span key={'view'}>
                <Accordion menuList={Menu} />{' '}
              </span>{' '}
            </div>{' '}
            {this.state.Addtaskfooter ? (
              <SaveCancelButton
                CancelButtonHandler={() =>
                  this.setState({
                    Addtaskfooter: false,
                  })
                }
                DoneButtonHandler={() => {
                  this.setState({
                    postcallofaddtask: true,
                  });
                  // this.setState({ Addtaskfooter: false })
                }}
              />
            ) : (
              ''
            )}{' '}
          </div>{' '}
        </div>
      );
    } else if (this.state.pageToNavigate === 'CreateTurntemplate') {
      return (
        <div>
          <CreateTurnTemplateHeader navigateback={this.handleback} />{' '}
          <div className="TurnManagement">
            <CreateTurnTemplate
              departmentresponse={this.props.departmentinitialresponse}
              stationscode={this.props.stationinitialresponse}
              dependency={this.props.taskinitialresponse}
              // templatedata={this.props.templateresponse}
              Equipfleetcatagory={this.Equipfleetcatagory(
                this.props.AircraftTypeList
              )}
              turntype={this.props.turntypeinitialresponse}
              CancelButtonHandler={() =>
                this.setState({
                  pageToNavigate: 'turnTemplate',
                })
              }
            />{' '}
            <div className="Manage">
              <span key={'create'}>
                <Accordion menuList={Menu} />{' '}
              </span>{' '}
            </div>{' '}
          </div>{' '}
        </div>
      );
    }
  };
  render() {
    console.log(this.props.templateresponse, 'templatetemplate');
    console.log(
      this.props.departmentinitialresponse,
      'departmentinitialresponse'
    );
    console.log(
      this.props.templateresponse,
      this.state.currentTemplateID,
      'templateresponsefli'
    );

    const val = this.props.templateresponse && this.props.templateresponse.data;
    if (this.state.currentTemplateID && val) {
      console.log(val, this.state.currentTemplateID, '-->currenttemplate');
      val.forEach(template => {
        if (template.id == this.state.currentTemplateID) {
          this.childTaskDetails = template;
        }
      });
    }
    const sort = this.sort(val);
    let templateidname = [];
    if (typeof this.childTaskDetails === 'object' && this.childTaskDetails) {
      templateidname.push(
        this.childTaskDetails.turntemplateid,
        this.childTaskDetails.turntemplatename
      );
    }

    return <div> {this.pageToNavigate(templateidname, sort)} </div>;
  }
}
export const Menu = [
  {
    title: 'Manage Department',
    data: ManageDepartment,
  },
  {
    title: 'Manage TurnType',
    data: ManageTurnType,
  },
  {
    title: 'Manage Task',
    data: ManageTask,
  },
];
const mapDispatchToProps = {
  templateinitialdata: TemplateListActions.getTemplateData,
  departmentinitialdata: ManageDepartmentActions.getDepartmentDetails,
  TurnTypeInitialdata: ManageTurnTypeActions.getTurnTypeDetails,
  taskinitialdata: ManageTaskActions.getTaskDetails,
  stationinitialdata: StationActions.getStationDetails,
  initialactypedata: AircraftTypeActions.getAircraftTypeDetails,
};

const mapStateToProps = state => {
  return {
    templateresponse: state.TurnTemplateInitialDatareducer,
    taskinitialresponse: state.Managetaskinitialdatareducer,
    turntypeinitialresponse: state.Manageturntypeinitialdatareducer,
    stationinitialresponse: state.Stationinitialdata,
    departmentinitialresponse: state.Managedepartmentinitialdatareducer,
    AircraftTypeList: state.Aircrafttypeinitialdata,
  };
};
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(FlightTurnManagement);
