import React, { Component } from 'react';
import './CreateButton.scss';

class CreateButton extends Component {
  state = {};

  clicked = () => {
    console.log('hi');
    if (
      this.props.CreateButtonHandler &&
      typeof this.props.CreateButtonHandler === 'function'
    ) {
      this.props.CreateButtonHandler();
    }
  };
  render() {
    return (
      <>
        <div className="CreateButton" onClick={this.clicked}>
          {this.props.text}
        </div>
      </>
    );
  }
}

export default CreateButton;
