import React, { Component } from 'react';
import { GoPlus } from 'react-icons/go';
import Button from '../../Atoms/Button';
import './AddTask.scss';

class AddTask extends Component {
  // constructor(props) {
  //   super(props);
  // }
  state = {};
  clicked = e => {
    if (this.props.AddTaskHandler && typeof this.props.AddTaskHandler) {
      this.props.AddTaskHandler(e);
    }
  };
  render() {
    return (
      <div className="addtask-conatainer" onClick={this.clicked}>
        <GoPlus className="addtask-create-icon" />
        <Button className={'addtask-content ' + this.props.className}>
          {this.props.children}
        </Button>
      </div>
    );
  }
}

export default AddTask;
