import React, { Component, useState, useEffect } from 'react';
import './SelectTasks.scss';
import SearchBox from '../../../../Atoms/SearchBox';
import Tick from '../../../../Img/tick.svg';
import Tickonclick from '../../../../Img/tickonclick.svg';
import Tabs from '../../../../Atoms/tab';
class SelectTasks extends Component {
  state = {
    searchselectedtask: '',
  };
  searchhandler = (e, list) => {
    console.log(e.target.value, 'searchhandler');
    this.setState({ searchselectedtask: e.target.value, list });
  };
  render() {
    console.log(this.props.departmentresponse, 'departmentresponse');
    console.log(
      this.state.searchselectedtask,
      this.state.list,
      'searchselectedtask'
    );
    return (
      <>
        <div className="task">
          <div className="searchcover">
            <SearchBox
              className="searchbox"
              placeholder="Search By Task Name"
              onChange={this.searchhandler}
              filterBy={['tasknm']}
            />
          </div>
          <div className="eng_ground_sec">
            <Tabs>
              {this.props.departmentresponse.data.map((item, index) => (
                // console.log(item.departmentcode)
                // eslint-disable-next-line react/jsx-key
                <div label={item.departmentcode}>
                  <div style={{ marginBottom: '50px' }}>
                    <Taskdetails
                      selectedtask={this.props.selectedtask.filter(selected => {
                        if (selected.departmentcode === item.departmentcode) {
                          return true;
                        }
                      })}
                      dependencytask={this.props.selectedtask}
                      searchselectedtask={this.state.searchselectedtask}
                      callback={this.props.callback}
                      task={'Maintask'}
                    ></Taskdetails>
                  </div>
                </div>
              ))}
            </Tabs>
          </div>
          {/* <div className='bottomline'></div> */}
        </div>
      </>
    );
  }
}
export default SelectTasks;

export const Taskdetails = ({
  selectedtask,
  searchselectedtask,
  callback,
  task,
  showorder,
}) => {
  const [tickhandler, setTickhandler] = useState([]);
  console.log(selectedtask, 'functional');
  let sort = [];
  selectedtask.map(item => {
    if (searchselectedtask === '') {
      sort = selectedtask;
      return;
    }
    if (item.tasknm.toLowerCase().match(searchselectedtask.toLowerCase()))
      sort.push(item);
  });

  // sort.map((maintask,index)=>{
  //     maintask.tick = "false"
  //     maintask.tasks=dependencytask.data;
  //     maintask.tasks.map((subtask,index)=>{
  //          subtask.tick = "false";
  //     })
  //     return maintask

  //  }
  // )

  console.log(sort, 'push');
  return (
    <>
      {sort.map((item, index) => (
        <>
          <div
            className={
              item.isActive && item.tick
                ? 'tasklist_addtask isactive'
                : 'tasklist_addtask'
            }
            key={index}
            onClick={() => {
              callback(item, task, index);
            }}
          >
            {console.log(item.isActive, 'is')}
            <div className="task_no_wing">
              <div className="taskno">{item.taskid}</div>
              <div className="wing">{item.taskgroup}</div>
            </div>
            <div className="task_name_details">
              <div className="task_name">{item.tasknm}</div>
              <div className="task_details">{item.taskdescription}</div>
            </div>
            <img
              src={item.tick ? (item.isActive ? Tick : Tickonclick) : Tick}
              className="tick"
            ></img>
          </div>
          <div className="bottomline_addtask"></div>
        </>
      ))}
    </>
  );
};

