import React, { Component } from 'react';
import CreateDepartment from '../CreateDepartment';
import DepartmentList from '../DepartmentList';
import CreateButton from '../../../../Atoms/CreateButton';
import { connect } from 'react-redux';
import { ManageDepartmentActions } from '../../../../../Store/ManageDepartment/action';

class ManageDepartment extends Component {
  state = {
    isCreateDepartment: false,
  };

  CreateDepartment = isOpen => {
    this.setState(prevState => {
      return { isCreateDepartment: isOpen };
    });
  };
  Departmentsubmit = obj => {
    console.log(obj, 'prakash1');
    this.props.Departmentsubmit(obj);
  };
  render() {
    return (
      <>
        {this.state.isCreateDepartment ? (
          <CreateDepartment
            CreateDepartment={this.CreateDepartment}
            Departmentsubmit={this.Departmentsubmit}
          />
        ) : (
          <CreateButton
            text="Create Department"
            CreateButtonHandler={() => {
              this.CreateDepartment(true);
            }}
          />
        )}
        <DepartmentList
          DepartmentResponse={this.props.departmentinitialresponse}
          handledelete={val => this.props.DepartmentDelete(val)}
        />
      </>
    );
  }
}
const mapDispatchToProps = {
  Departmentsubmit: ManageDepartmentActions.postDepDetails,
  DepartmentDelete: ManageDepartmentActions.deleteDepartmentDetails,
};
const mapStateToProps = state => {
  return {
    departmentinitialresponse: state.Managedepartmentinitialdatareducer,
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(ManageDepartment);
