import React, { Component } from 'react';
import './CreateDepartment.scss';
import InputElement from '../../../../Atoms/InputElement';
import Button from '../../../../Atoms/Button';
import { commonAction } from '../../../../../Store/commonActivity/action';
import '../../../../../Utils/Aircraft.scss';

class CreateDepartment extends Component {
  obj = {
    departmentname: '',
    departmentcode: '',
  };
  checkallfields = () => {
    if (this.obj.departmentname === '') {
      commonAction.openAlert({
        title: 'Department',
        content: 'Department Name Must be Filled',
      });
      return false;
    } else if (this.obj.departmentcode === '') {
      commonAction.openAlert({
        title: 'Department',
        content: 'Department Code Must be Filled',
      });
      return false;
    }
    this.props.CreateDepartment(false);
    this.props.Departmentsubmit(this.obj);
  };
  render() {
    console.log(this.obj, 'CreateDepartment');
    return (
      <>
        <div>
          <div className="CreateDepartmentText">Create Department</div>
          <div className="DepartmentDetails">
            {/* <div className='DepartmentText'>Department ID</div>
              <InputElement className='DepartmentInputelements' placeholder='Enter' disabled  /> */}
            <div style={{ display: 'flex ' }}>
              <div className="DepartmentText">Department Name</div>
              <div className="mandatory-star">&nbsp;*</div>
            </div>
            <InputElement
              className="DepartmentInputelements"
              placeholder="Enter"
              onChange={e => {
                this.obj.departmentname = e.target.value;
              }}
            />
            <div style={{ display: 'flex ' }}>
              <div className="DepartmentText">Department Code</div>
              <div className="mandatory-star">&nbsp;*</div>
            </div>
            <InputElement
              className="DepartmentInputelements"
              placeholder="Enter"
              onChange={e => {
                this.obj.departmentcode = e.target.value.toUpperCase();
              }}
            />
            <div className="buttondesign">
              <Button
                className="save-btn"
                ButtonHandler={() => {
                  this.checkallfields();
                }}
              >
                Save
              </Button>{' '}
              <Button ButtonHandler={() => this.props.CreateDepartment(false)}>
                Cancel
              </Button>
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default CreateDepartment;
