import React from 'react';
import CreateTask from '../CreateTask';
import './ManageTask.scss';
import InputElement from '../../../../Atoms/InputElement';
import CreateButton from '../../../../Atoms/CreateButton';
import TaskList from '../TaskList';
import { MdSearch } from 'react-icons/md';
import { connect } from 'react-redux';
import { ManageTaskActions } from '../../../../../Store/ManageTask/action';
import SearchBox from '../../../../Atoms/SearchBox';

class ManageTask extends React.Component {
  state = {
    isCreateTask: false,
    AccTasksearch: '',
  };
  createtask = isOpen => {
    this.setState(prevState => {
      return { isCreateTask: isOpen };
    });
  };
  TaskSubmit = obj => {
    console.log('task', obj);
    this.props.TaskSubmit(obj);
  };
  AccTasksearch = e => {
    this.setState({ AccTasksearch: e.target.value });
  };
  sort(val) {
    let iteration = val;
    if (this.state.AccTasksearch !== '') {
      iteration = val.filter(item => {
        if (
          item.tasknm
            .toLowerCase()
            .match(this.state.AccTasksearch.toLowerCase())
        ) {
          return item;
        }
      });
    }
    return iteration;
  }
  render() {
    const val = this.props.taskinitialresponse.data;
    console.log(this.props.departmentinitialresponse.data, 'dd');
    console.log(this.state.AccTasksearch, 'acctask');
    const sort = this.sort(val);
    return (
      <>
        {this.state.isCreateTask ? (
          <div>
            <div className="createTasks">Create Tasks</div>
            <CreateTask
              createtask={this.createtask}
              TaskSubmit={this.TaskSubmit}
              departdropdown={this.props.departmentinitialresponse.data}
            />
          </div>
        ) : (
          <CreateButton
            text="Create Task"
            CreateButtonHandler={() => {
              this.createtask(true);
            }}
          />
        )}
        <div className="TaskHeading">
          <div className="TaskList">Task List</div>
          <div className="task-search-header">
            {/* <MdSearch className="task-search-icon" />
            <InputElement
              className="searchbartask"
              placeholder="Search by name"
              onChange={e => this.AccTasksearch(e.target.value)}
            /> */}
            <SearchBox
              className="searchbartask"
              placeholder="Search by name"
              list={val}
              onChange={this.AccTasksearch}
              filterBy={['tasknm']}
            />
          </div>
        </div>
        <div className="task1">
          <TaskList
            TaskResponse={sort}
            handledelete={val => this.props.TaskDelete(val)}
          />
        </div>
      </>
    );
  }
}

const mapDispatchToProps = {
  TaskSubmit: ManageTaskActions.postTaskDetails,
  TaskDelete: ManageTaskActions.deleteTaskDetails,
};
const mapStateToProps = state => {
  return {
    taskinitialresponse: state.Managetaskinitialdatareducer,
    departmentinitialresponse: state.Managedepartmentinitialdatareducer,
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(ManageTask);
