import React, { Component } from 'react';
import './CreateTask.scss';
import Radio from '../../../../Atoms/Radio';
import Button from '../../../../Atoms/Button';
import InputElement from '../../../../Atoms/InputElement';
import { Dropdown } from '../../../../Atoms/DropdownComponent';
import { commonAction } from '../../../../../Store/commonActivity/action';

class CreateTask extends Component {
  obj = {
    taskgroup: '',
    tasknm: '',
    taskdescription: '',
    iscaamsenable: '',
    departmentcode: '',
    alert: '',
  };

  checkallfields = () => {
    if (this.obj.taskgroup === '') {
      commonAction.openAlert({
        title: 'TASK',
        content: 'Task Group Must be Filled',
      });
      return false;
    } else if (this.obj.tasknm === '') {
      commonAction.openAlert({
        title: 'TASK',
        content: 'Task Name Must be Filled',
      });
      return false;
    } else if (this.obj.taskdescription === '') {
      commonAction.openAlert({
        title: 'TASK',
        content: 'Task Description Must be Filled',
      });
      return false;
    } else if (this.obj.iscaamsenable === '') {
      commonAction.openAlert({
        title: 'TASK',
        content: 'Caams Enable Must be Filled',
      });
      return false;
    } else if (this.obj.alert === '') {
      commonAction.openAlert({
        title: 'TASK',
        content: 'Alert Must be Filled',
      });
      return false;
    } else if (this.obj.departmentcode === '') {
      commonAction.openAlert({
        title: 'TASK',
        content: 'Responsible Department Must be Filled',
      });
      return false;
    }

    this.props.createtask(false);
    this.props.TaskSubmit(this.obj);
  };

  render() {
    console.log(this.props.departdropdown, 'liyu');
    let deptarray = [];
    this.props.departdropdown.forEach(dropdowndata => {
      deptarray.push({
        title: dropdowndata.departmentname,
        selected: false,
      });
    });

    let dataArray = [
      {
        text: 'ATW',
      },
      {
        text: 'BTW',
      },
    ];
    let caamsyesnoarray = [
      {
        text: 'YES',
      },
      {
        text: 'No',
      },
    ];
    let alertyesnoarray = [
      {
        text: 'YES',
      },
      {
        text: 'No',
      },
    ];
    return (
      <>
        <div className="TaskBody">
          <div style={{ display: 'flex ' }}>
            <div className="Textcolor">Task Group</div>
            <div className="mandatory-star">&nbsp;*</div>
          </div>
          <Radio
            name="click"
            dataList={dataArray}
            radioHandler={e => (this.obj.taskgroup = e)}
          />
          <div style={{ display: 'flex ' }}>
            <div className="Textcolor Textpadding">Task Name</div>
            <div className="mandatory-star Textpadding">&nbsp;*</div>
          </div>
          <InputElement
            className="inputbox"
            placeholder="Enter"
            onChange={e => {
              this.obj.tasknm = e.target.value;
            }}
          />
          <div style={{ display: 'flex ' }}>
            <div className="Textcolor Textpadding">Task Description</div>
            <div className="mandatory-star Textpadding">&nbsp;*</div>
          </div>
          <InputElement
            className="inputbox"
            placeholder="Enter"
            onChange={e => {
              this.obj.taskdescription = e.target.value;
            }}
          />
          <div style={{ display: 'flex ' }}>
            <div className="Textcolor Textpadding">CAAMS Enable</div>
            <div className="mandatory-star Textpadding">&nbsp;*</div>
          </div>
          <Radio
            name="cammsyesno"
            dataList={caamsyesnoarray}
            radioHandler={e => {
              if (e === 'YES') {
                this.obj.iscaamsenable = 'Y';
              } else {
                this.obj.iscaamsenable = 'N';
              }
            }}
          />
          <div style={{ display: 'flex ' }}>
            <div className="Textcolor Textpadding">ALERT</div>
            <div className="mandatory-star Textpadding">&nbsp;*</div>
          </div>
          <Radio
            name="alertyesno"
            dataList={alertyesnoarray}
            radioHandler={e => {
              if (e === 'YES') {
                this.obj.alert = 'Y';
              } else {
                this.obj.alert = 'N';
              }
            }}
          />
          <div style={{ display: 'flex ' }}>
            <div className="Textcolor Textpadding">Responsible Department</div>
            <div className="mandatory-star Textpadding">&nbsp;*</div>
          </div>
          <Dropdown
            ReturnSelected={e => {
              this.obj.departmentcode = e;
            }}
            title="Select Department"
            list={deptarray}
            placeholder="Select Department"
            toggleItem={item => {
              this.props.departdropdown.map((dep, i) => {
                if (item.title === dep.departmentname) {
                  this.obj.departmentcode = dep.departmentcode;
                }
              });
            }}
          />
          <div className="buttondesign">
            <Button
              className="save-btn"
              ButtonHandler={() => {
                this.checkallfields();
              }}
            >
              Save
            </Button>
            <Button ButtonHandler={() => this.props.createtask(false)}>
              Cancel
            </Button>
          </div>
        </div>
      </>
    );
  }
}

export default CreateTask;
