import React from 'react';
import '../Radio/Radio.scss';

class Radio extends React.Component {
  handler = e => {
    if (this.props.radioHandler) {
      this.props.radioHandler(e.target.value);
    }
  };

  render() {
    console.log(this.props);
    return (
      <>
        {this.props.dataList.map((data, index) => {
          return (
            <>
              <input
                key={index}
                type="radio"
                name={this.props.name}
                id={this.props.name + index}
                value={data.text}
                onChange={e => this.handler(e)}
              />
              <label htmlFor={this.props.name + index}>{data.text}</label>
            </>
          );
        })}
      </>
    );
  }
}
export default Radio;
