import React, { Component } from 'react';
import CreateTemplateTemplateDetails from '../CreateTemplateTemplateDetails';
import SelectTasks from '../SelectTasks';
import Dependencytask from '../Dependencytask';
import './CreateTurnTemplate.scss';
import SaveCancelButton from '../../../../Atoms/SaveCancelButton';
import moment from 'moment';
import { connect } from 'react-redux';
import { ManageCreateTurnTemplate } from '../../../../../Store/TurnManagement/CreateTurnTemplate/action';
import { commonAction } from '../../../../../Store/commonActivity/action';

class CreateTurnTemplate extends Component {
  state = {
    TemplateDetails: {},
    subtaskresponse: false,
    Maintask: [],
    selectedOrderForMainTask: [],
    selectedDependency: [],
  };
  Validation =(finalobj)=>{
    console.log(finalobj,"validation")
  if (finalobj.turntemplatename === undefined || finalobj.turntemplatename === "") {
    commonAction.openAlert({
      title: 'Create Template',
      content: 'Template Name is Mandatory',
    });
    return false;
  } else if (finalobj.turntype === '') {
    commonAction.openAlert({
      title: 'Create Template',
      content: 'TurnType is Mandatory',
    });
    return false;
  }
  else if (finalobj.stationcd === '') {
    commonAction.openAlert({
      title: 'Create Template',
      content: 'Station Name  is Mandatory',
    });
    return false; 
  }
  else if (finalobj.plannedtat == '') {
    commonAction.openAlert({
      title: 'Create Template',
      content: 'PlannedTaT is Mandatory',
    });
    return false;
  } else if (finalobj.airlineaircrafttype === '') {
    commonAction.openAlert({
      title: 'Create Template',
      content: 'Equipment Type is Mandatory',
    });
    return false;
  } else if (finalobj.tasks.length < 1) {
    commonAction.openAlert({
      title: 'Create Template',
      content: 'Atleast One Task Should be Selected',
    });
    return false;
  }
  this.props.CreateTurnTemplate(finalobj);
  this.props.CancelButtonHandler();
  console.log(finalobj, 'finalone');

}

  generateTurn = Maintask => {
    let SubTask = [...this.props.dependency.data]; //41

    let task = Maintask.map((maintask, index) => {
      const temp = {
        ...maintask,
      };
      temp.tick = false;
      temp.isActive = false;
      // temp.dependencyOrder = [];
      temp.dependencytaskid = [];
      temp.cpminclind = false;
      temp.taskstart = '';
      temp.taskend = '';
      let SubTasktemp = [];
      SubTask.forEach((item, i) => {
        const newItem = {
          ...item,
        };

        newItem.parent = temp.taskid;
        newItem.tick = false;
        if (newItem.taskid !== temp.taskid) {
          SubTasktemp.push(newItem);
        }
      });
      temp.tasks = [...SubTasktemp];
      return temp;
    });
    this.setState({
      Maintask: task,
    });
  };

  callback = (item, taskname, index) => {
    console.log(item, taskname, index);
    let temp = [...this.state.Maintask];
    let { selectedOrderForMainTask = [], selectedDependency } = this.state;
    if (taskname === 'Maintask') {
      temp = temp.map(val => {
        val.isActive = false;
        if (val.taskid === item.taskid) {
          item.tick = !item.tick;

          if (item.tick) {
            item.isActive = true;
            selectedDependency = item.tasks;
            let isAlreadySelected = false;
            selectedOrderForMainTask.forEach(selected => {
              if (selected.taskid == item.taskid) {
                isAlreadySelected = true;
              }
            });
            if (!isAlreadySelected) {
              selectedOrderForMainTask.push(item);
            }
          } else {
            selectedOrderForMainTask = selectedOrderForMainTask.filter(
              selected => selected.taskid != item.taskid
            );
          }
          return item;
        }

        return val;
      });
      this.setState({
        Maintask: temp,
        selectedDependency,
        selectedOrderForMainTask,
      });
    } else {
      console.log(temp, item, 'tempitem');
      item.tick = !item.tick;
      temp.forEach(main => {
        if (main.taskid === item.parent) {
          if (item.tick) {
            main.dependencytaskid.push(item.taskid);
            // main.dependencyOrder.push(item.taskid);
          } else {
            main.dependencytaskid = main.dependencytaskid.filter(
              val => !(val === item.taskid)
            );
            // main.dependencyOrder = main.dependencyOrder.filter(
            //   val => !(val === item.taskid)
            // );
          }
        }
      });
    }

    // selectedOrderForMainTask.dependencytaskid;

    this.setState({});

    // this.setState({ Maintask: task });
  };

  CreateTemplatedetails = obj => {
    this.setState({
      TemplateDetails: obj,
    });
  };

  DCPHandler = currentparent => {
    //   alert();
    console.log(currentparent.cpminclind, 'currentparent');
    currentparent.isDCPinclude = !currentparent.isDCPinclude;
    currentparent.cpminclind = currentparent.isDCPinclude;
    console.log(currentparent.cpminclind, 'currentparentAFTER');
    this.setState({});
  };

  durationHandler = (currentparent, value, condition) => {
    //   alert();

    if (condition === 'start') {
      currentparent.taskstart = value;
    } else {
      currentparent.taskend = value;
    }
    this.setState({});
  };

  ////for making post call
  DoneButtonHandler = () => {
    let currentdate =
      moment(new Date())
        .toISOString()
        .split('.')[0] + 'Z';
    console.log(currentdate, 'Createddate');

    // copyobject = this.state.TemplateDetails;

    let task = this.state.Maintask.filter(val => val.tick === true);
    task = task.map((item, i) => {
      let copyobject = {
        ...item,
      };
      if (copyobject.tick === true) {
        copyobject.taskorder = i + 1;
        if (copyobject.cpminclind === true) {
          copyobject.cpminclind = 'Y';
        } else {
          copyobject.cpminclind = 'N';
        }
        copyobject.dependencytaskid = copyobject.dependencytaskid.map(
          (item, index) => {
            return {
              taskid: item,
              taskorder: index + 1,
            };
          }
        );
        copyobject.createddate = currentdate;
        copyobject.lastmodifieddate = currentdate;
        delete copyobject['tasks'];
        delete copyobject['tick'];
        delete copyobject['isActive'];
        delete copyobject['isDCPinclude'];
        delete copyobject['id'];
      }
      console.log(copyobject, 'copyobject');
      return copyobject;
    });

    let finalobj = this.state.TemplateDetails;

    this.props.turntype.data.map((item, i) => {
      if (finalobj.turntype === item.turntype) {
        finalobj.turntypeid = item.id;
      }
    });

    console.log(finalobj, 'finaltask');
    finalobj.createddate = currentdate;
    finalobj.lastmodifieddate = currentdate;
    finalobj.tasks = task;
    finalobj.tasks.map(item => {
      let striged = item.dependencytaskid.map(string => {
        return JSON.stringify(string).replace(/"/g, "'");
      });
      item.dependencytaskid = striged;
      return item;
    });

    this.Validation(finalobj);

  };

  render() {
    console.log(this.state.Maintask, 'maintask');
    console.log(this.props.turntype, 'turntypeid');
    let Maintask = this.props.dependency.data || []; //41
    let { selectedOrderForMainTask } = this.state;
    let currentparent = [];
    if (selectedOrderForMainTask.length) {
      currentparent =
        selectedOrderForMainTask[selectedOrderForMainTask.length - 1];
      currentparent.isActive = true;
      selectedOrderForMainTask = currentparent.tasks;
    }
    console.log(currentparent, 'current parent');
    if (this.state.Maintask.length !== Maintask.length) {
      this.generateTurn(Maintask);
    }

    let station = [];
    this.props.stationscode.map((item, index) => {
      station.push({
        title: item.station,
        selected: false,
      });
    });

    console.log(this.state.Maintask, 'Maintask');
    console.log(this.state.selectedDependency, 'selectedDependency');
    console.log(
      this.state.selectedOrderForMainTask,
      'selectedOrderForMainTask'
    );

    ///FOR SHOWING HOLE TASK COUNT //
    let counter = 0;
    this.state.Maintask.map((item, index) => {
      if (item.tick === true) {
        counter = counter + 1;
      }
    });

    return (
      <>
        <div className="Create-Turn-Template">
          <CreateTemplateTemplateDetails
            turntype={this.props.turntype}
            // templatedata={this.props.templatedata}
            stationscode={station}
            CreateTemplatedetails={obj => {
              this.CreateTemplatedetails(obj);
            }}
            Equipfleetcatagory={this.props.Equipfleetcatagory}
          />
          <div className="Selecttasktext"> Selected Tasks - {counter} </div>{' '}
          <div
            style={{
              display: 'flex',
            }}
          >
            <SelectTasks
              selectedtask={this.state.Maintask}
              callback={this.callback}
              departmentresponse={this.props.departmentresponse}
            />{' '}
            <Dependencytask
              selectedtask={selectedOrderForMainTask}
              mainTask={this.state.Maintask}
              mainTaskCurrent={currentparent}
              callback={this.callback}
              DCPHandler={this.DCPHandler}
              durationHandler={this.durationHandler}
            />{' '}
          </div>{' '}
        </div>
        <SaveCancelButton
          CancelButtonHandler={() => this.props.CancelButtonHandler()}
          DoneButtonHandler={() => {
            this.DoneButtonHandler();
          }}
        />{' '}
      </>
    );
  }
}

const mapDispatchToProps = {
  CreateTurnTemplate: ManageCreateTurnTemplate.postNewTemplate,
};
const mapStateToProps = state => {
  return {};
};
export default connect(mapStateToProps, mapDispatchToProps)(CreateTurnTemplate);
