import React from 'react';
import { ReactComponent as ChevronUp } from '../../../assets/Icon/chevronup.svg';
import { ReactComponent as ChevronDown } from '../../../assets/Icon/chevrondown.svg';
import { TiTick } from 'react-icons/ti';
import './DropDown.scss';

export default class Dropdown extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      listOpen: false,
      headerTitle: this.props.title,
      bold: false,
    };
    this.close = this.close.bind(this);
  }

  componentDidUpdate() {
    const { listOpen } = this.state;
    setTimeout(() => {
      if (listOpen) {
        window.addEventListener('click', this.close);
      } else {
        window.removeEventListener('click', this.close);
      }
    }, 0);
  }

  componentWillUnmount() {
    window.removeEventListener('click', this.close);
  }

  toggleList() {
    this.setState(prevState => ({
      listOpen: !prevState.listOpen,
    }));
  }

  close() {
    this.setState({
      listOpen: false,
    });
  }

  render() {
    const { list, toggleItem, className } = this.props;
    const { listOpen, headerTitle } = this.state;
    return (
      <div className={`${className} dd-wrapper`}>
        {this.props.dropdownDisable ? (
          <div
            className="dd-header disable-bg"
            onClick={() => this.toggleList()}
          >
            <div
              className={
                this.state.bold ? 'dd-header-title-bold' : 'dd-header-title'
              }
            >
              {headerTitle}
            </div>
            <span>
              <ChevronDown />
            </span>
          </div>
        ) : (
          <div>
            <div className="dd-header" onClick={() => this.toggleList()}>
              <div
                className={
                  this.state.bold || this.props.defaultValue
                    ? 'dd-header-title-bold'
                    : 'dd-header-title'
                }
              >
                {this.state.bold
                  ? headerTitle
                  : this.props.defaultValue
                  ? this.props.defaultValue
                  : headerTitle}
              </div>
              <span>{listOpen ? <ChevronUp /> : <ChevronDown />}</span>
            </div>
            {listOpen && (
              <ul className="dd-list">
                {list.map(item => (
                  <li
                    className="dd-list-item"
                    key={item.title}
                    onClick={() => {
                      this.setState({ headerTitle: item.title, bold: true });
                      toggleItem(item);
                    }}
                  >
                    {item.title}{' '}
                    {item.selected && (
                      <span>
                        <TiTick />
                      </span>
                    )}
                  </li>
                ))}
              </ul>
            )}
          </div>
        )}
      </div>
    );
  }
}

Dropdown.defaultProps = {
  title: 'Select',
  list: [],
  toggleItem: () => {},
  className: '',
};
