import React, { Component } from 'react';
import './TaskList.scss';
import DeleteIcon from './../../../../Img/delete.png';

class TaskList extends Component {
  state = {};
  obj = {
    tasknm: '',
    taskgroup: '',
  };
  render() {
    const val = this.props.TaskResponse;

    return (
      <>
        <div className="Task">
          {val.map((value, index) => (
            <div className="Task-item" key={index}>
              <div className="TaskNumber">
                {value.id}
                <div className="Taskrow2">{value.taskgroup}</div>
              </div>
              <div className="TaskNumber TaskDescription">
                {value.tasknm}
                <div className="Taskrow2">{value.taskdescription}</div>
              </div>
              <div className="TaskCategory">
                {value.departmentcode}
                {/* {value.deptid} */}
                <div className="delete-icon-div">
                  <img
                    onClick={() => this.props.handledelete({ id: value.id })}
                    src={DeleteIcon}
                    width="20px"
                    height="20px"
                    alt="delete"
                  />

                  {/* <div
                        className="btn-style pad-16l "
                        onClick={() => this.handleClick(index)}
                      >
                        <img
                          src={EditIcon}
                          width="20px"
                          height="20px"
                          alt="edit"
                        />
                      </div> */}
                </div>
              </div>
            </div>
          ))}
        </div>
      </>
    );
  }
}

export default TaskList;
